import axios from 'axios';

async function getPosts(commit, storage = []){
    try{
        let url = 'https://getpantry.cloud/apiv1/pantry/48772c43-095e-44c0-b987-edab68e5bbda/basket/postsDate';
        let response = await axios.get(url);
        console.log('getting posts data from server...');
        if(response.data.postsDate){
            // localStorage.setItem('postsDate', JSON.stringify(response.data.postsDate))
            if(storage?.length  && storage == response.data.postsDate){
                commit(storage)
            }
            else{
                commit(response.data.postsDate)
            }
            
            return response.data.postsDate
        } 
            
        
    }catch(e){
        commit(storage, false)
        console.log("Bad connection...");
    }
}
export default getPosts